._Loading div {
    font-size: 4vw;
      font-family: 'Lucida Grande', Helvetica, Arial, sans-serif;
      color: #333;
      display: block; 
      margin: 0;
      width: 100%;
      height: auto;
      text-align: center
  }
._Loading {
  display : block;
  width: 50%;
  margin: 7vh 0 0 22.5vw;
}
.img-dots{
  margin-top: 3vh;
  margin-left: 0.7vw;
  display: block;
}
._advertise{
  display: block;
}
  
  ._Loading  span {
      display: block; 
      margin: 0 auto;
  }
  
  ._Loading span:nth-child(odd) {
    background: #004879;
  }
  ._Loading span:nth-child(even) {
    background: #D22E1E;
  }
  ._Loading span[class*="dot"] {
      height: 1.5vw; width: 1.5vw;
      
      display: inline-block;
      margin: 12px 0.8vw;
      
      border-radius: 100%;
      -webkit-border-radius: 100%;
      -moz-border-radius: 100%;
      
      -webkit-animation: loader 4s infinite;
      -webkit-animation-timing-function: cubic-bezier(0.030, 0.615, 0.995, 0.415);
      -webkit-animation-fill-mode: both;
      -moz-animation: loader 4s infinite;
      -moz-animation-timing-function: cubic-bezier(0.030, 0.615, 0.995, 0.415);
      -moz-animation-fill-mode: both;
      -ms-animation: loader 4s infinite;
      -ms-animation-timing-function: cubic-bezier(0.030, 0.615, 0.995, 0.415);
      -ms-animation-fill-mode: both;
      animation: loader 4s infinite;
      animation-timing-function: cubic-bezier(0.030, 0.615, 0.995, 0.415);
      animation-fill-mode: both;
  }
  
  span.dot1 {-webkit-animation-delay: 1s;      animation-delay: 1s;      -ms-animation-delay: 1s;      -moz-animation-delay: 1s;}
  span.dot2 {-webkit-animation-delay: 0.8s;    animation-delay: 0.8s;    -ms-animation-delay: 0.8s;    -moz-animation-delay: 0.8s;}
  span.dot3 {-webkit-animation-delay: 0.6s;    animation-delay: 0.6s;    -ms-animation-delay: 0.6s;    -moz-animation-delay: 0.6s;}
  span.dot4 {-webkit-animation-delay: 0.4s;    animation-delay: 0.4s;    -ms-animation-delay: 0.4s;    -moz-animation-delay: 0.4s;}
  span.dot5 {-webkit-animation-delay: 0.2s;    animation-delay: 0.2s;    -ms-animation-delay: 0.2s;    -moz-animation-delay: 0.2s;}
  span.dot6 {-webkit-animation-delay: 0;       animation-delay: 0;       -ms-animation-delay: 0;       -moz-animation-delay: 0;}
  
  @-webkit-keyframes loader {
      0% {-webkit-transform: translateX(-30px); opacity: 0;}
      25% {opacity: 1;}
      50% {-webkit-transform: translateX(30px); opacity: 0;}
      100% {opacity: 0;}
  }
  
  @-keyframes loader {
      0% {-transform: translateX(-30px); opacity: 0;}
      25% {opacity: 1;}
      50% {-transform: translateX(30px); opacity: 0;}
      100% {opacity: 0;}
  }

